/**
 * @file router
 * @author huanghuiquan(huanghuiquanhhh@gmail.com)
 */

import Vue from 'vue';
import Router from 'vue-router';
import * as types from './store/mutation-types';

// 
let Home = () => import('@/pages/Home.vue');
let Detail = () => import('@/pages/Detail.vue');
let NotFound = () => import('@/pages/NotFound.vue');
let User = () => import('@/pages/User.vue');
let Search = () => import('@/pages/Search.vue');
let Favor = () => import('@/pages/Favor.vue');

Vue.use(Router);

export function createRouter() {
    let router = new Router({

        // history  /
        mode: 'hash',
        /* eslint-disable no-undef */
        base: __webpack_public_path__,
        /* eslint-enable no-undef */
        routes: [
            {
                path: '/',
                name: 'home',
                component: Home
            },
            {
                path: '/detail/:nid',
                name: 'detail',
                component: Detail,
                meta: {
                    swipeBack: true
                    // notKeepAlive: true
                }
            },
            {
                path: '/user',
                name: 'user',
                component: User
            },
            {
                path: '/search',
                name: 'search',
                component: Search,
                meta: {
                    swipeBack: true
                }
            },
            {
                path: '/favor',
                name: 'favor',
                component: Favor,
                meta: {
                    swipeBack: true
                }
            },
            {
                path: '*',
                name: 'notFound',
                component: NotFound
            }
        ]
    });

    /**
     * 
     *
     * @type {string}
     * @const
     */
    const SLIDE_LEFT = 'slide-left';

    /**
     * 
     *
     * @type {string}
     * @const
     */
    const SLIDE_RIGHT = 'slide-right';

    router.beforeEach((to, from, next) => {
        if (router.app.$store) {

            // 
            if (router.app.$store.state.appShell.needPageTransition) {

                // 
                let pageTransitionName = isForward(to, from) ? SLIDE_LEFT : SLIDE_RIGHT;
                router.app.$store.commit(`appShell/${types.SET_PAGE_TRANSITION_NAME}`, {pageTransitionName});
            }
        }
        next();
    });

    return router;
}

/**
 * to 
 *
 * @type {Array.<string>}
 * @const
 */
const ALWAYS_BACK_PAGE = ['home'];

/**
 * to 
 *
 * @type {Array.<string>}
 * @const
 */
const ALWAYS_FORWARD_PAGE = [];

/**
 *  fullPath
 *
 * @type {Array.<string>}
 * @const
 */
const HISTORY_STACK = [];

/**
 * true 
 *
 * @param {Object} to  route
 * @param {Object} from  route
 * @return {boolean} 
 */
function isForward(to, from) {

    // to 
    if (to.name && ALWAYS_BACK_PAGE.indexOf(to.name) !== -1) {

        // 
        HISTORY_STACK.length = 0;
        return false;
    }

    if (from.name && ALWAYS_BACK_PAGE.indexOf(from.name) !== -1) {

        //  ALWAYS_BACK_PAGE 
        HISTORY_STACK.push(to.fullPath);
        return true;
    }

    if (to.name && ALWAYS_FORWARD_PAGE.indexOf(to.name) !== -1) {

        // to 
        HISTORY_STACK.push(to.fullPath);
        return true;
    }

    //  fullPath 
    let index = HISTORY_STACK.indexOf(to.fullPath);
    if (index !== -1) {
        HISTORY_STACK.length = index + 1;
        return false;
    }

    //  to.fullPath 
    HISTORY_STACK.push(to.fullPath);
    return true;
}



